@import url('https://fonts.googleapis.com/css2?family=Anton&family=Monoton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster+Two&display=swap');


:root {
  width: 100%;
  
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  font-family: 'Lobster Two', cursive;
  color: white;
  background-color: rgba(0,0,0,.5);
  overflow: hidden;
  position: relative;
  z-index: 1;
}
.background-container{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
.background-container img{
  width: 100vw;
  height: 100vh;
  object-fit: fill;

}
.game-container{
  position: relative;
  z-index: 20;
  padding:2rem;
  background-color: rgba(0,0,0,0.2);
  box-shadow: 10px 10px rgb(0 0 0 / 5%);
}
header{
  text-align: center;
  font-family: 'Monoton', cursive;
  color: #fb5959;
}
header h1{
  letter-spacing: .54rem;
  font-size: 3rem;
  line-height: 4rem;
}

header h3{
  letter-spacing: .30rem;
  font-size: 1.8rem;
  line-height: 1.8rem;
}
.board-container{
  width: 100%;
  padding: 1em 0;
}

.squares{
  padding: 1rem ;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  max-width: 350px;
  max-height: 100px;
  
}
.squares button{
  max-width: 100px;
  width: 33%;
  min-height: 80px;
  display: flex;
  align-items: center;
  background: none;
  justify-content: center;
  border: 2px solid white;
  font-size: 1rem;
}
.squares button img{
  width: 100%;
  width: 33%;
  transform: scale(2) rotate(965deg) rotate3d(1, 1, 1, 45deg);
}
.active{
  width: 100%;
  max-width: 20px;
  min-height: 20px;
  display: block;
}
.not-active{
  display: none;
}
.game-manage{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contestant-score{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  justify-content: space-around;
  font-size: 1.2rem;
}
.contestant-score h3{
  margin: 0 5px 0 0;
}
.new-game-btn{ 
  max-width: 150px;
  width: 40%;
  margin: 0.75rem;
  padding: 0.75rem;
  font-size: 0.8rem;
  background: none;
  border:none;
  background-color: #fb5959;;
  color: #ffffff;
  font-family: 'Monoton', cursive;
}

.winner-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  letter-spacing: 1.1px;
}

@media (max-width: 521px) {

  header h1{
    letter-spacing: 0.4rem;
    font-size: 2rem;
    line-height: 3rem;
  }
  
  header h3{
    letter-spacing: .24rem;
    font-size: 1.2rem;
    line-height: 1.4rem;
  }
  .contestant-score{
    font-size: 1.2rem;
    line-height: 1.8rem;
    letter-spacing: .2rem;
  }
  .squares button img{
    transform: scale(1.5) rotate(965deg) rotate3d(1, 1, 1, 45deg);
  }
  .new-game-btn{
    font-size: .65rem;
  }
}
@media (min-width: 522px) and (max-width: 1024px) {

  
  header h1{
    letter-spacing: 0.6rem;
    font-size: 2.2rem;
    line-height: 3.5rem;
  }
  
  header h3{
    letter-spacing: .44rem;
    font-size: 1.4rem;
    line-height: 1.8rem;
  }
  .contestant-score{
    font-size: 1.2rem;
    line-height: 1.4rem;
    letter-spacing: .2rem;
  }

}

